.header {
    display: flex;
    padding: 15px 20px;
    justify-content: space-between;
    position: -webkit-sticky;
    position: sticky;
    background-color: white;
    z-index: 100;
    top: 0;
    box-shadow: 0px 5px 8px -9px rgb(0, 0, 0, 0.75);
}

.header-left {
    display: flex;
    justify-content: space-between;
}

.header-input {
    display: flex;
    align-items: center;
    background-color: #eff2f5;
    padding: 10px;
    margin-left: 10px;
    border-radius: 999px;
}

.header-input>input {
    border: none;
    background-color: transparent;
    outline-width: 0;
}

.header-left>img {
    height: 40px;
}

.header-meddle {
    display: flex;
    flex: 1;
    justify-content: center;
}

.header-option {
    display: flex;
    align-items: center;
    padding: 0 30px;
    cursor: pointer;
}

.header-option:hover {
    background-color: #eff2f5;
    border-radius: 10px;
    align-items: center;
    padding: 0 30px;
    border-bottom: none;
}

.header-option--active>.MuiSvgIcon-root {
    color: #2e81f4 !important;
}

.header-option--active {
    border-bottom: 4px solid #2e81f4;
}

.header-option>.MuiSvgIcon-root {
    color: gray;
}

.header-option:hover>.MuiSvgIcon-root {
    color: #2e81f4;
}

.header-right {
    display: flex;
}

.hader-info {
    display: flex;
    align-items: center;
}

.hader-info>h4 {
    margin-left: 10px;
}